'use client';
import React from 'react';
import { useRouter } from 'next/navigation';
import { SalonDirectory } from '@/modules/SalonDirectory';

export default function DirectoryPage() {
    const router = useRouter();

    const handleSelectSalon = (salonId: string) => {
        // Redirect to public booking page for that salon
        // /booking/[salonId]
        router.push(`/booking/${salonId}`);
    };

    return (
        <SalonDirectory
            onSelectSalon={handleSelectSalon}
            onBack={() => router.push('/login')}
            onAdminAccess={(salonId) => router.push(`/login?context=tenant&salonId=${salonId}`)}
        />
    );
}
